import uniGrid from "@/components/uni-grid/uni-grid.vue"
import uniGridItem from "@/components/uni-grid-item/uni-grid-item.vue"
export default {
	data: function() {
		return {
			list: [],
			hotList: [],
			listAll: [],
		}
	},
	components: {
		uniGrid,
		uniGridItem
	},
	mounted: function() {
		this.getHot()
	},
	methods: {
		getHot: function() {
			let apiUrl = getApp().globalData.apiUrl;
			Promise.all([
				this.getData("新歌榜", apiUrl + "/top/list?idx=27"),
				this.getData("热歌榜", apiUrl + "/top/list?idx=1"),
				this.getData("飙升榜", apiUrl + "/top/list?idx=3"),

			]).then(res => {
				res.forEach(res => this.listAll.push(res))
				console.log(this.listAll)
			})
		},
		getDtail: function(e) {
			uni.navigateTo({
				url: "../playing/index?id=" + e.detail.index
			})
		},
		getData: function(name, url) {
			//创建Promise 实例时立即执行
			return new Promise((resolve, reject) => {
				uni.request({
					url: url,
					success: (res) => {
						let arr = res.data.playlist.tracks
						resolve({
							name: name,
							list: arr.filter((item, index) => {
								if (index < 6) return item
							})
						})
					}
				})
			})
		}
	}
}
